.AboutContainer {
    background-color: #ECD8CF;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.AboutSpecial {

    position: relative;
    width: 100%;
    justify-content: center;
}

.AboutCard {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0px 0px 40px 0px;
}

.AboutText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    margin-top: 55px;
    text-align: center;
}

.AboutHeading {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    color: #ECD8CF;
    background-color: #1D5A40;
    font-size: xx-large;
    width: 80%;
    height: 80px;
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    border-radius: 10px 10px 10px 10px;
    box-shadow: rgba(0, 106, 44, 0.4) 10px 0px,
    rgba(0, 106, 44, 0.4) -10px 0px,
     rgba(0, 106, 44, 0.3) 20px 0px,
     rgba(0, 106, 44, 0.3) -20px 0px,
     rgba(0, 106, 44, 0.2) 30px 0px,
     rgba(0, 106, 44, 0.2) -30px 0px,
       rgba(0, 106, 44, 0.1) 40px 0px,
       rgba(0, 106, 44, 0.1) -40px 0px,
        rgba(0, 106, 44, 0.05) 50px 0px,
        rgba(0, 106, 44, 0.05) -50px 0px;
}


.AboutPara {
    color: #1D5A40;
    width: 800px;
    margin: 50px 0px 50px 0px;
    font-size: x-large;
    font-family: 'Lucida Sans Unicode';
}

.Aboutimg {
    width: 30%;
    align-items: center;
    align-content: center;
    display: flex;
    position: relative;
    justify-content: center;
    border-radius: 50px;
}

.aboutImage {
    width: 100%;
    border: 5px solid #ffffff;
    border-radius: 10%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 10px;

}

.aboutImage:hover {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 15px 50px;
    transition: all 0.5s ease;
}


.AboutStats {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 30px;
    text-align: center;
    font-size: x-large;
    color: #1D5A40;
}

.fa {
    height: 100px;
    margin: 35px 10px 35px 10px;
}

.StatsCards {
    max-height: 450px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 20%;
    border: 5px solid #ffffff;
    border-radius: 10%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 10px;
    margin: 20px;
}

.StatsCards:hover {
    box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, 
    rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
    transition: all 0.5s ease;


}

.Instructor {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    color: #ECD8CF;
    background-color: #1D5A40;
    font-size: xx-large;
    width: 40%;
    height: 80px;
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    border-radius: 10px 10px 10px 10px;
    box-shadow: rgba(0, 106, 44, 0.4) 10px 0px,
    rgba(0, 106, 44, 0.4) -10px 0px,
     rgba(0, 106, 44, 0.3) 20px 0px,
     rgba(0, 106, 44, 0.3) -20px 0px,
     rgba(0, 106, 44, 0.2) 30px 0px,
     rgba(0, 106, 44, 0.2) -30px 0px,
       rgba(0, 106, 44, 0.1) 40px 0px,
       rgba(0, 106, 44, 0.1) -40px 0px,
        rgba(0, 106, 44, 0.05) 50px 0px,
        rgba(0, 106, 44, 0.05) -50px 0px;
}

.AboutInstructor {
    display: flex;
    justify-content: center;
}

