@media screen and (max-width:1000px) {

    .AboutContainer {
        width: 100%;
        overflow-x: auto;
    }

    .AboutCard {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        width: 100%;
        margin: 0px 0px 40px 0px;
    }

    .Aboutimg {
        width: 70%;
        align-items: center;
        align-content: center;
        align-self: center;
        display: flex;
        position: relative;
        justify-content: center;
        border-radius: 50px;
    }

    .aboutImage {
        display: flex;
        align-items: center;
        align-self: center;
        width: 100%;
        border: 5px solid #ffffff;
        border-radius: 10%;
    }

    .AboutHeading {
        font-size: large;
        width: 50%;
        height: 80px;
    }

    .AboutPara {
        width: 50%;
        font-size: large;
        /* font-family: Montserrat; */
    }

    .AboutStats {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 30px;
        text-align: center;
        font-size: small;
        color: #1D5A40;
    }

    .fa {
        margin: 10px 10px 30px 10px;
        height: 40px;}

    .StatsCards {
        padding: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 180px;
        width: 60%;
        border: 5px solid #ffffff;
        border-radius: 10%;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 10px;
        margin: 20px;
    }

    .Instructors {
        display: none;
    }
    .Instructorss {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .Instructor {
        font-size: x-large;
        width: 60%;
        height: 80px;
    }

    .AboutInstructor {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        align-content: center;
    }

    .InstructorCard {
        border-radius: 50px;
        display: flex;
        flex-direction: column;
        margin: 20px 0px 20px 0px;
        min-width: 80%;
        max-width: 30%;
        background-color: #ffffff;
    }

    .InstructorCard0 {
        height: 50%;
        border-radius: 50px;
        display: flex;
        flex-direction: column;
        margin: 100px 0px 20px 0px;
        min-width: 80%;
        max-width: 30%;
        background-color: #ffffff;
    }

    .Instructorimg{
        height: 100%;
        width: 100%;
        border-radius: 50px;
    }

    .InstructorImage {
        width: 100%;
        height: 100%;
        border-radius: 50px 50px 0px 0px;
    }

    .Instructorimg0 {
    height: 100%;
    width: 100%;
    border-radius: 50px;
    }

    .InstructorImage0 {
        width: 100%;
        height: 100%;
        border-radius: 50px 50px 0px 0px;
    }
}